<!--柱状图-->
<template>
    <div class="echarts-bar">
<!--        <div id="main" style="width: 600px;height:600px;"></div>-->
<!--      <div id="mywordcloud" :style="{width: '100%', height: '300px'}" :data="worddata"></div>-->
      <div id="myEchart" :style="{width: '300px', height: '300px'}"></div>

    </div>


</template>

<script>
//import echarts from "echarts";
import * as echarts from "echarts";
import "echarts-wordcloud/dist/echarts-wordcloud";
import "echarts-wordcloud/dist/echarts-wordcloud.min";
export default {
  data(){
    return{
      image:"data:image/png;base64,"
    }

  },
  mounted(){
    this. initEchart()
  },
  methods:{
    initEchart(){
      const echartDom = document.getElementById('myEchart')
      const myChart = echarts.init(echartDom)
      var options
      var maskImage = new Image();
      maskImage.src = this.image

      options  = {
        series: [{
          type: 'wordCloud',
          shape: 'circle',
          keepAspect: false,
          maskImage: maskImage,
          left: 'center',
          top: 'center',
          width: '100%',
          height: '90%',
          right: null,
          bottom: null,
          sizeRange: [4, 8],
          rotationRange: [-90, 90],
          rotationStep: 45,
          gridSize: 8,
          drawOutOfBound: false,
          layoutAnimation: true,
          textStyle: {
            fontFamily: 'sans-serif',
            fontWeight: 'bold',
            color: function () {
              return 'rgb(' + [
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160)
              ].join(',') + ')';
            }
          },
          emphasis: {
            // focus: 'self',
            textStyle: {
              textShadowBlur: 3,
              textShadowColor: '#333'
            }
          },
          //data属性中的value值却大，权重就却大，展示字体就却大
          data: [
            {name: 'Farrah',value: 366},
            {name: "汽车",value: 900},
            {name: "视频",value: 606},
            {name: "科技", "value": 452},
            {name: "美食", "value": 789},
            {name: "旅游", "value": 321},
            {name: "音乐", "value": 567},
            {name: "健康", "value": 245},
            {name: "时尚", "value": 678},
            {name: "体育", "value": 543},
            {name: "房地产", "value": 876},
            {name: "电影", "value": 432},
            {name: "教育", "value": 987},
            {name: "金融", "value": 345},
            {name: "游戏", "value": 654},
            {name: "新闻", "value": 789},
            {name: "艺术", "value": 456},
            {name: "文化", "value": 321},
            {name: "科学", "value": 567},
            {name: "历史", "value": 234},
            {name: "政治", "value": 678},
            {name: "环境", "value": 543},
            {name: "社交媒体", "value": 876},
            {name: "宠物", "value": 432},
            {name: "家居", "value": 987},
            {name: "美容", "value": 345},
            {name: "摄影", "value": 654},
            {name: "舞蹈", "value": 789},
            {name: "军事", "value": 456},
            {name: "自然", "value": 321},
            {name: "风景", "value": 567},
            {name: "人物", "value": 234},
            {name: "动物", "value": 678},
            {name: "编程", "value": 543},
            {name: "设计", "value": 876},
            {name: "旅行", "value": 432},
            {name: "心理学", "value": 987},
            {name: "医学", "value": 345},
            {name: "运动", "value": 654},
            {name: "购物", "value": 789},
            {name: "电视剧", "value": 456},
            {name: "文学", "value": 321},
            {name: "植物", "value": 567},
            {name: "天文学", "value": 234},
            {name: "烹饪", "value": 678},
            {name: "戏剧", "value": 543},
            {name: "法律", "value": 876},
            {name: "心理健康", "value": 432},
            {name: "音乐剧", "value": 987},
            {name: "化学", "value": 345}
          ]
        }]

      }
      myChart.setOption(options)

      //随着屏幕大小调节图表
      window.addEventListener("resize", () => {
        myChart.resize();
      });
    }
  }
}


</script>
